.container-login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgb(102, 45, 253);
  background: linear-gradient(0deg, rgba(102, 45, 253, 0) 0%, rgba(203, 226, 241, 0.8) 0%);
  z-index: 1;
}

.container-login form {
  background: rgba(250, 251, 253, 0.95);
  padding: 15px;
  margin: 5px;
  border: 1.5px solid rgb(59, 125, 239);
  border-radius: 10px;
  box-shadow: 0 3px rgba(59, 125, 239, 0.6);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 70%;
  color: rgb(59, 125, 239)
}

.container-login fieldset {
  padding: 9px;
}

.container-login h3 {
  margin-bottom: 10px;
  text-align: center;
}

.container-login label {
  margin-bottom: 5px;
  display: flex;
  justify-content: center;
  font-size: 10px;
}

.container-login input {
  border-radius: 3px;
  padding: 5px;
  outline: 0;
  opacity: 0.7;
}

form .button {
  border: 1px solid blue;
  border-radius: 5px;
}

.fatherbtn {
  margin-top: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
}